import classes from "./Counter.module.css";
import { useSelector, useDispatch } from "react-redux";
import { counterActions } from "./store/index";

const Counter = () => {
  const dispatch = useDispatch();
  const counter = useSelector((state) => state.counter.counter); // used for getting data from the state
  const show = useSelector((state) => state.counter.showCounter);

  const toggleCounterHandler = () => {
    dispatch(counterActions.toggleCounter());
  };

  const incrementHandler = () => {
    dispatch(counterActions.incrment());
  };

  const increaseHandler = () => {
    dispatch(counterActions.increase(10)); // {type:"Some unique ID" payload: 10}
  };

  const decrementHandler = () => {
    dispatch(counterActions.decrement());
  };

  const miltiplicationBy2Handler = () => {
    dispatch(counterActions.multiply());
  };

  return (
    <main className={classes.counter}>
      <h1>Redux Counter</h1>
      {show && <div className={classes.value}>{counter}</div>}
      <div>
        <button onClick={incrementHandler}>incrment</button>
        <button onClick={increaseHandler}>incrment by 10</button>
        <button onClick={decrementHandler}>decrement</button>
        <button onClick={miltiplicationBy2Handler}>Multiply</button>
      </div>
      <button onClick={toggleCounterHandler}>Toggle Counter</button>
    </main>
  );
};

export default Counter;
